import { useState } from "react";
import { Link } from "react-router-dom";
import "./Header.scss";

const Header = () => {
  const [linkFilm, setLinkFilm] = useState("");
  function search(e) {
    e.preventDefault();
  }

  return (
    <div className="header">
      <Link to={"MovieAPP/"} className="header__brand">
        Move app
      </Link>
      <form className="header__search">
        <input
          type="text"
          placeholder="Посик"
          onChange={(e) => setLinkFilm(e.target.value)}
        />
        <Link
          to={`films/${linkFilm}`}
          className="button"
          onClick={() => search()}
        >
          Search
        </Link>
      </form>
    </div>
  );
};

export default Header;
