import { useEffect } from "react";
import { Link } from "react-router-dom";
import { dataFilm } from "../../data/obj";
import "./Films.scss";

const Films = () => {
  return (
    <div className="films__content">
      <h1>Подборка фильмов</h1>
      {dataFilm.map((el, idx) => {
        return (
          <div key={idx}>
            <Link to={el.kinopoiskId.toString()}>
              <img src={el.posterUrlPreview} alt={el.nameEn} />
            </Link>
          </div>
        );
      })}
    </div>
  );
};

export default Films;
