import { Link, useParams } from "react-router-dom";
import { useEffect, useState } from "react";
import InfiniteCarousel from "react-leaf-carousel";

import "./AboutFilm.scss";

const AboutFilm = () => {
  const params = useParams();
  const [isNewLink, setIsNewLink] = useState(params.kinopoiskId);

  const API_URL_VIDIO = `https://kinopoiskapiunofficial.tech/api/v2.2/films/${isNewLink}`;
  const API_URL_SIMILARS = `https://kinopoiskapiunofficial.tech/api/v2.2/films/${isNewLink}/similars`;

  const API_TOKEN = "4c9256a4-f1db-437b-9d26-69752332dae8";
  const [film, setFilm] = useState();
  const [similars, setSimilars] = useState({});
  const [isLoading, setIsLoading] = useState(true);
  const [isLoadingMain, setIsLoadingMain] = useState(true);

  useEffect(() => {
    window.scrollTo({ top: 0 });
    setIsLoading(true);
    setIsLoadingMain(true);
    const getSimilarMovie = async () => {
      try {
        const res = await fetch(
          `https://kinopoiskapiunofficial.tech/api/v2.2/films/${params.kinopoiskId}/similars`,
          {
            method: "GET",
            headers: {
              "Content-Type": "application/json",
              "X-API-KEY": API_TOKEN,
              "Access-Control-Allow-Origin": "*",
              "Access-Control-Allow-Headers": "X-Requested-With",
            },
          }
        );
        const resData = await res.json();
        console.log(resData);
        setSimilars(resData);
        setIsLoading(false);
      } catch (err) {
        return <h1>Failed to fetch 402</h1>;
      }
    };
    getSimilarMovie();

    const getfilm = async () => {
      try {
        const result = await fetch(
          `https://kinopoiskapiunofficial.tech/api/v2.2/films/${params.kinopoiskId}`,
          {
            method: "GET",
            headers: {
              "Content-Type": "application/json",
              "X-API-KEY": API_TOKEN,
            },
          }
        );

        const resultData = await result.json();
        console.log(resultData);
        setFilm(resultData);
        setIsLoadingMain(false);
      } catch (err) {
        return <h1>Failed to fetch 402</h1>;
      }
    };

    getfilm();
  }, [isNewLink]);

  /*
  useEffect(() => {
    const getSimilarMovie = async () => {
      try {
        const res = await new Promise((resolve, reject) => {
          setTimeout(() => {
            const items = filmsSil;
            resolve(items);
            setIsLoading(false);
          }, 2000);
        });
        const resData = await res;
        console.log(resData);
        setSimilars(resData);
      } catch (err) {
        return <h1>Failed to fetch 402</h1>;
      }
    };
    getSimilarMovie();

    const getfilm = async () => {
      try {
        const result = await new Promise((resolve, reject) => {
          setTimeout(() => {
            const items = dataFilm.find((el) => {
              return el.kinopoiskId.toString() === params.kinopoiskId;
            });
            resolve(items);
            setIsLoadingMain(false);
          }, 2000);
        });
        /*
        

        const resultData = await result;
        console.log(resultData);
        setFilm(resultData);
      } catch (err) {
        return <h1>Failed to fetch 402</h1>;
      }
    };

    getfilm();
  }, []);
  
*/
  /*const film = dataFilm.find((el) => {
    return el.kinopoiskId.toString() === params.kinopoiskId;
  });
*/
  if (isLoadingMain) {
    return <h1>Loading...</h1>;
  } else {
    return (
      <div className="pageFilm">
        <div className="pageFilm__header">
          <img src={film.logoUrl} alt="" />
          <p className="header__slogan">{film.slogan}</p>
        </div>
        <div className="pageFilm__content">
          <div className="pageFilm__poster">
            <img
              className="pageFilm__poster-item"
              src={film.posterUrl}
              alt={film.nameOriginal}
            />
          </div>
          <div className="pageFilm__info">
            <h2>{film.nameRu ? film.nameRu : film.nameOriginal}</h2>
            <div className="pageFilm__info-rating">
              <p>Imdb {film.ratingImdb}</p>
              <p>Кинопоиск {film.ratingKinopoisk}</p>
            </div>

            <div className="pageFilm__info-descShort">
              <p style={{ fontSize: "15px", color: "whitesmoke" }}>
                {film.shortDescription}
              </p>
            </div>
            <div className="pageFilm__info-genere">
              {film.genres.map((el, idx) => {
                return <p key={idx}>{el.genre}</p>;
              })}
            </div>
            <p className="pageFilm__info-year">{`${film.year}${
              film.countries.length !== 0
                ? ", " + film.countries[0].country
                : ""
            }`}</p>
            <a
              className="button pageFilm__button"
              href={`https://yandex.ru/search/?text=смотреть ${film.nameOriginal}`}
              target="_blank"
            >
              Смотреть
            </a>
            <a
              className="button-gray pageFilm__button"
              href={`https://www.youtube.com/results?search_query=трейлер ${film.nameOriginal}`}
              target="_blank"
            >
              Трейлер
            </a>
          </div>
        </div>
        <div className="pageFilm__desription">
          <p style={{ fontSize: "15px", color: "whitesmoke" }}>
            {film.description}
          </p>
        </div>
        <div className="pageFilm__related-videos">
          {isLoading ? (
            <h1>Loading...</h1>
          ) : (
            <>
              <div className="pageFilm__wraper-info">
                <p>Похожие фильмы</p>
                <p>{similars.total ? similars.total : ""}</p>
              </div>

              {similars.items.length < 1 ? (
                <p className="pageFilm__no-result">Ничего не найденно</p>
              ) : (
                <InfiniteCarousel
                  arrows={true}
                  showSides={true}
                  sidesOpacity={0.5}
                  sideSize={1.2}
                  slidesToScroll={4}
                  slidesToShow={2}
                  scrollOnDevice={true}
                  autoCycle={true}
                  animationDuration={900}
                  cycleInterval={5000}
                >
                  {similars.items.map((el, idx) => {
                    return (
                      <Link
                        to={`./../${el.filmId.toString()}`}
                        key={idx}
                        onClick={() => {
                          setIsNewLink(el.filmId);
                        }}
                      >
                        <img
                          className="itemCarousel"
                          src={el.posterUrlPreview}
                          alt={idx}
                        />
                      </Link>
                    );
                  })}
                </InfiniteCarousel>
              )}
            </>
          )}
        </div>
      </div>
    );
  }
};

export default AboutFilm;
